html,
body {
  padding: 0;
  margin: 0;
  font: caption;
}

body {
  background-color: black;
  color: #eee;
}

canvas {
  width: 100%;
}

.download {
  margin: 0 auto;
  padding: 0 2em;
}

.download h2 {
  font-size: 1.1rem;
  margin: 0;
}

.download h3 {
  font-size: 1.1rem;
  margin: 0;
}

.download section {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.download ul {
  margin-left: 0;
  padding-left: 0;
  list-style-type: none;
}
.download ul li::before {
  content: attr(data-marker);
  display: inline-block;
  text-align: center;
  width: 25px;
}

a {
  color: #ddd;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

footer {
  text-align: center;
  margin: 2em;
}
footer span {
  white-space: nowrap;
}
